









export default {
  name: 'App',
  mounted() {
    //  1vh
    let vh = window.innerHeight * 0.01

    // CSS
    // --vh  1vhcss(100vh) height: calc(var(--vh, 1vh)*100)
    document.documentElement.style.setProperty('--vh', `${vh}px`)

    //  resize
    window.addEventListener('resize', () => {
      let vh = window.innerHeight * 0.01
      // console.log(vh);
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    })
  },
}
